<template>
    
    <div>

    	<c-title :hide="false" text='收货地址管理'></c-title>
    	<div style="height: 50px;"></div>
    	         <!--<c-addressList :datasource="list"></c-addressList>-->
        <ul v-for="(item,itemIndex) in datasource" @click="selectedAddress(itemIndex)">
            <el-row :gutter="5">
            <el-col :span="2" :offset="1"><br/><el-checkbox v-model="checkList[itemIndex]"  @change="setDefaultAddress(itemIndex)"></el-checkbox></el-col>
            <el-col :span="16"><font style="font-size: 1rem;">{{item.province}}    {{item.city}}</font><br/> <font style="font-size: 0.85rem;color: rgba(118, 127, 138, 1);">{{item.district}}    {{item.address}}<br/>{{item.username}} {{item.mobile}}</font>
            </el-col><br/>            
            <el-col :span="5"><i class="fa fa-trash-o" @click="deleteHandler(item,itemIndex)"></i><i class="fa fa-pencil-square-o" @click="alterHandler(itemIndex)"></i></el-col>
            </el-row>     
        </ul>
        <div style="height: 50px;"></div>
    	<div class="address_addnav" @click="addendAddress"><i class="fa fa-plus-circle"></i><span>新增收货地址</span></div>         

    </div>
   
</template>
<script>
import address_controller from './address_controller';
export default address_controller;

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.address_addnav{  width:100%;  padding: 0 3% 0 6%!important;position: fixed;bottom: 0;left: 0;background: #f15353 !important;color: #fff !important;text-align: center;height: 44px !important;line-height: 44px !important;z-index: 7;}
.address_addnav i{    color: #fff;
    font-size: 22px;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 18px;
    line-height: 18px;
    margin-left: -74px;
    margin-top: -9px;
}
.fa-trash-o{margin-right: 20px;font-size: 1.3rem;}
.fa-pencil-square-o{font-size: 1.3rem;}
.el-row{background: #FFF;padding: 10px 0;border-bottom: solid 1px #ccc; 
	.el-col{vertical-align: middle;}
	.el-col-16{text-align: left;padding-left: 5px;}
	.el-checkbox,i,el-radio radio{line-height: 36px;}
	}
.address_addnav i{color: #fff;  font-size: 22px;  position: absolute;  top: 50%;  height: 18px;  line-height: 18px;  margin-left: -74px;  margin-top: -9px;}	
</style>


