<template>
      
    <div>
         
          <el-row :gutter="5" v-for="(item,itemIndex) in datasource">
          <el-col :span="2" :offset="1"><el-checkbox v-model="checkList[itemIndex]"  @change="setDefaultAddress(itemIndex,$event)"></el-checkbox></el-col>
          <el-col :span="17">{{item.province}}{{item.city}}<br/>{{item.region}}{{item.region}}</el-col>
          <el-col :span="4"><i class="fa fa-trash-o" style="margin-right: 10px;"></i><i class="fa fa-pencil-square-o"></i></el-col>
         </el-row> 
        
    </div>
        
            
</template>
<script>

import { Loadmore } from 'mint-ui';
import { Cell } from 'mint-ui';
//import { CellSwipe } from 'mint-ui';
  export default {
    //select_inde-当前选择行,datasource-数据源,currentdata-当前选择的数据
     props: ['select_index','datasource','currentdata'],
    data() {
      return {
      	checkList:[false,false]

      }
    },
    methods: {
      setDefaultAddress (itemIndex,e)
      {
        
       
        for(let i=0;i<this.checkList.length;i++)
        {
          this.checkList[i] = false;
        }
        this.checkList[itemIndex] =  true;
        
        console.log(this.checkList);  

      }
   
      
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.el-row{background: #FFF;padding: 10px 0;border-bottom: solid 1px #ccc; 
	.el-col{vertical-align: middle;}
	.el-col-17{text-align: left;padding-left: 5px;}
	.el-checkbox,i,el-radio radio{line-height: 36px;}
	}
</style>


