
import cTitle from 'components/title';
import cAddressList from 'components/addressList';
import { MessageBox } from 'mint-ui';
import { mapState, mapMutations } from 'vuex';
export default {
  props: {
    currentAddress: String
  },
  data() {
    return {
      lastIndex: -1,
      defaultIndex: 0,
      activeName: 'first',
      checkList: [],
      datasource: []
    };
  },

  methods:
  {
    //设置默认地址
    setDefaultAddress(itemIndex) {
      if (this.lastIndex < 0) {
        //首次进入页面
        this.lastIndex = itemIndex;
        this.checkList[this.defaultIndex] = false;
        this.checkList[itemIndex] = true;
      } else {
        this.checkList[this.lastIndex] = false;
        this.checkList[itemIndex] = true;
        this.lastIndex = itemIndex;
      }
      
      this.postData(this.datasource[itemIndex].id);


    },
    selectedAddress(index) {
      
       if (this.lastIndex < 0) {
        //首次进入页面
        for(let i = 0;i<this.datasource.length;i++)
        {
          this.$set(this.checkList,i,false);

        }
        this.lastIndex = index;
        this.$set(this.checkList,index,true);
        //this.checkList[itemIndex] = true;
      } else {
        //this.checkList[this.lastIndex] = false;
        this.$set(this.checkList,this.lastIndex,false);
        this.$set(this.checkList,index,false);
        //this.checkList[index] = true;
        this.lastIndex = index;
      } 
      // this.$set(this.checkList,this.lastIndex,false);
      //  this.$set(this.checkList,index,true);
      //  this.lastIndex = index;
       this.postData(this.datasource[index].id);
    },
    //添加地址
    addendAddress() {
      this.$router.push('/member/appendAddress');

    },
    //删除地址
    deleteHandler(item, itemIndex) {

      MessageBox({
        title: '提示',
        message: '确定删除此收货地址?',
        showCancelButton: true
      }).then(action => {
        if (action == 'confirm') {
          //删除当前选中的地址
          console.log(item);
          var that = this;
          $http.get('member.member-address.destroy', { address_id: item.id }, true).then(function (response) {
            that.datasource.splice(that.datasource.indexOf(item), 1);
            that.checkList.splice(itemIndex, 1);
          }, function (response) {
            // error callback
            MessageBox('提示', response.msg);
          });

        } else {
          return;
        }

      });

    },
    //修改收货地址
    alterHandler(itemIndex) {
      this.$router.push({ name: 'alterAddress', params: { model: this.datasource[itemIndex] } });

    },
    getAddress() {
      var that = this;
      $http.get('member.member-address.index').then(function (response) {
        
        if (response.result == 1) {
          for (var i = 0; i < response.data.length; i++) {
            var item = response.data[i];
            if (item.isdefault == 1) {
              that.checkList.push(true);

            } else {
              that.checkList.push(false);

            }

          }
          that.datasource = response.data;
          console.log(response.data);

        } else {
          console.log(response.msg);
        }
      }, function (response) {
        // error callback
      });
    },
    //设置默认地址的
    postData(address_id)
    {
      //member.member-address.setDefault
      alert(address_id);
      var that = this;
      $http.get('member.member-address.setDefault', {address_id:address_id }).then(function (response) {
        
        if (response.result == 1) {
          for (var i = 0; i < response.data.length; i++) {
            var item = response.data[i];
            if (item.isdefault == 1) {
              that.checkList.push(true);

            } else {
              that.checkList.push(false);

            }

          }
          that.datasource = response.data;
          

        } else {
          console.log(response.msg);
        }
      }, function (response) {
        // error callback
      });
    }

  },
  mounted() {


  },
  created() {


  },
  activated() {
    this.getAddress();
  }
  ,
  components: { cTitle, cAddressList }
};


